
.rik-fail {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 0;
  width: 100%;

  &__container {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap-reverse;
    width: 100%;
  }
}

.rik-fail-video {
  position: relative;
  width: 80px;
  height: 130px;
  overflow: hidden;
  transform: translateY(50%);
  transition: 500ms ease;

  &.active {
    transform: translateY(0);
  }

  &__inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    video,
    img {
      position: absolute;
      top: 0;
      left: 50%;
      width: auto;
      height: 100%;
      transform: translateX(-48%);
    }
  }
}